.main-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  padding: 20px;
}

.content-container {
  display: flex;
  justify-content: space-between;
  width: 80%;
  gap: 20px; /* Añadimos un espacio entre las tarjetas */
  margin: 0 auto;
}

/* Añadimos una media query para pantallas pequeñas */
@media (max-width: 768px) {
  .content-container {
    flex-direction: column; /* Las tarjetas se apilan verticalmente */
    width: 100%; /* Aseguramos que ocupen el 100% del ancho en móviles */
  }

  .card {
    width: 100%; /* Cada tarjeta ocupará todo el ancho disponible */
  }
}

.card {
  flex: 1;
  background-color: #f4f4f4;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Opcional: Ajusta el tamaño del botón en pantallas pequeñas */
@media (max-width: 768px) {
  .cipher-button {
    font-size: 1em; /* Ajustamos el tamaño de la fuente en botones para pantallas pequeñas */
    padding: 10px 20px; /* Reducimos el padding para pantallas pequeñas */
  }
}

.cipher-button {
  background-color: #61dafb;
  border: none;
  padding: 15px 30px;
  font-size: 1.2em;
  color: #282c34;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 20px;
  align-self: center;
}

.cipher-button:hover {
  background-color: #21a1f1;
}
