.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Asegura que la aplicación cubra toda la pantalla */
}

.App-header {
  background-color: #282c34;
  height: 60px; /* Ajusta el tamaño del header */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000; /* Para que el header esté siempre encima */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.App-footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
}

main {
  flex: 1; /* El contenido principal se expande */
  overflow-y: auto; /* Permite el desplazamiento vertical si es necesario */
  padding: 20px;
}

/* Logo animado (opcional) */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
