/* Contenedor principal para el contenido de Cifrado César */
.cesar-content {
  display: flex;
  justify-content: space-around; /* Los contenedores tendrán espacio alrededor en pantallas grandes */
  align-items: flex-start;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  gap: 40px; /* Espacio entre los dos contenedores principales */
  min-height: 100vh; /* Ocupa al menos el 100% de la altura de la ventana */
  overflow-y: auto; /* Permite el desplazamiento vertical */
  padding-bottom: 40px; /* Añade espacio extra en la parte inferior */
}

/* Paneles izquierdo y derecho en pantallas grandes */
.left-side, .right-side {
  width: 45%; /* El ancho es del 45% del contenedor en pantallas grandes */
  padding: 30px;
  background-color: #f4f4f4;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  min-height: 350px; /* Ajusta la altura mínima para evitar inconsistencias */
}

/* Ajuste específico para el panel derecho */
.right-side {
  background-color: #f0f0f0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Ajustes para pantallas pequeñas (menos de 768px) */
@media (max-width: 768px) {
  .cesar-content {
    flex-direction: column; /* Los contenedores se apilan uno encima del otro */
    gap: 20px; /* Espacio entre los contenedores apilados */
  }

  .left-side, .right-side {
    width: 100%; /* Ocupan el 100% del ancho en pantallas pequeñas */
  }

  button {
    width: 100%; /* Los botones ocuparán todo el ancho en pantallas pequeñas */
    margin-top: 10px;
  }
}

.input-group {
  margin-bottom: 20px;
  width: 100%;
}

textarea, select {
  width: 100%;
  padding: 10px;
  font-size: 1em;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.button-group {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #21a1f1;
}
